import PictureAsPdfIcon from "@material-ui/icons/PictureAsPdf";
import React from "react";
import { Grid } from "@material-ui/core";
import CardActionArea from "@material-ui/core/CardActionArea";
import { makeStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import "./docs.css";

/* Create CSS Styles
 *
 * Description: Creates CSS styles for
 * 	- root
 *
 */
const useStyles = makeStyles({
	root: {
		borderRadius: "0px",
		margin: 0,
	},
});

/* Exports a formatted div which links to a certain document
 * 
 * Description: given its set of paramaters, the function exports
 * a responsive div acting as a link to target document
 * 
 * @param fullWidth
 * @param nume = name of document
 * @param link = link to actual document
 * @param dimensiune = size of div
 * @param tip = format of document
 * 
 * @retval div acting as link
 */
export default function Document({
	fullWidth = false,
	nume,
	link,
	dimensiune,
	tip = "pdf",
}) {
	// Apply styles
	const classes = useStyles();

	return (
		<>
			<Grid item xs={12} sm={12} md={fullWidth ? 12 : 6}>
				<div className="Doc">
					<a href={"https://api.osut.ro/media/docs/" + link}>
						<div className="Document unselectable">
							{/* <PictureAsPdfIcon color="default" className="iconpdf" /> */}
							<Typography
								color="textPrimary"
								variant="body1"
								component="div"
								style={{
									textTransform: "none",
									fontWeight: 400,
								}}>
								{nume}
								<div className="smaller">
									<span className="categorie">{tip}</span> &middot;&nbsp;
									<span>{dimensiune}</span>
								</div>
							</Typography>
						</div>
					</a>
				</div>
			</Grid>
		</>
	);
}
