import React from "react";
import Typography from "@material-ui/core/Typography";
import "./etichete.css";

/* Returns a note with a certain name and size
 *
 * Description: Given a certain name and size
 * the function returns a Topography with
 * preset settings.
 * 
 * @param denumire = string containing size of ettiquete
 * @param small = bool denoting its size
 * 
 * @retval Topography
 * 
 * Note: see Topography documentation
 */
const Eticheta = ({ denumire, small = false }) => {
	let tag = "tag_mic";

	if(! denumire instanceof String ||
	   ! small instanceof Boolean) {
			console.log("ERROR | Attempting to instantiate Eticheta on invalid type!");

			if(!denumire instanceof String)
				denumire = "";
			if(!small instanceof Boolean)
				small = false;
	}

	if(!small)
		tag = "tag";

	return (
		<>
			<Typography
				className={tag}
				color="textSecondary"
				variant="body2"
				component="span">
				{denumire}
			</Typography>
		</>
	);
};

export default Eticheta;
